import { Fragment } from 'react'
import { connect } from 'react-redux'
import { CheckCircleIcon, XIcon } from '@heroicons/react/solid'
import { useState } from 'react'

function Alert ({
    alert
}){

    let claseStyle1 = 'rounded-md bg-green-50 p-4'
    let claseStyle2 = 'h-5 w-5 text-green-400'
    let claseStyle3 = 'text-sm font-medium text-green-800'

    if (alert !== null && alert.alertType ==='red'){
        claseStyle1 = 'rounded-md bg-red-50 p-4'
        claseStyle2 = 'h-5 w-5 text-red-400'
        claseStyle3 = 'text-sm font-medium text-red-800'
    }
    const displayAlert = () => {
        if (alert !== null && (alert.alertType ==='green' || alert.alertType ==='red')){
            return (
                <div className={claseStyle1}>
                <div className="flex">
                    <div className="flex-shrink-0">
                    <CheckCircleIcon className={claseStyle2} aria-hidden="true" />
                    </div>
                    <div className="ml-3">
                    <p className={claseStyle3}>{alert.msg}</p>
                    </div>
                </div>
                </div>
            ) 
        } else {
            return(
                <Fragment></Fragment>
            )
        }
    }

    return ( 
        <Fragment>
            {displayAlert()}
        </Fragment>
    )
}

const mapStateToProps = state =>({
    alert: state.Alert.alert
})

export default connect(mapStateToProps)(Alert)